<template>
    <div class="table-responsive" v-for="standing in currentStandings">
        <table class="table table-sm align-middle">
                <thead>
                    <tr>
                        <th colspan="10"><img class="standings-league-logo" v-bind:src="standing.league_logo" alt="league-logo" />{{standing.league_country}}: {{standing.league_name}}</th>
                    </tr>
                    
                </thead>
                <tbody v-if="standing.league_type == 'League'">
                    <tr class="standings-header">
                        <td></td>
                        <td></td>
                        <td>MP</td>
                        <td>W</td>
                        <td>D</td>
                        <td>L</td>
                        <td>G</td>
                        <td>+/-</td>
                        <td>P</td>
                        <td></td>
                    </tr>
                    
                        <tr class="team-rank-row" v-for="leagueRank in standing.standings">
                            <td class="standings-rank">{{leagueRank.rank}}</td>
                            <td class="standings-team-name"><img class="standings-team-logo" v-bind:src="leagueRank.team_logo" alt="team-logo" /> {{leagueRank.team_name}}</td>
                            <td>{{leagueRank.all_played}}</td>
                            <td>{{leagueRank.all_win}}</td>
                            <td>{{leagueRank.all_draw}}</td>
                            <td>{{leagueRank.all_lose}}</td>
                            <td>{{leagueRank.all_goals_for}}:{{leagueRank.all_goals_against}}</td>
                            <td>{{leagueRank.goals_diff}}</td>
                            <td>{{leagueRank.points}}</td>
                            <td>
                                <span v-if="leagueRank.form[0] == 'W'">
                                    <span class="d-inline badge text-bg-success form-separator">{{leagueRank.form[0]}}</span>
                                </span>
                                
                                <span v-if="leagueRank.form[0] == 'D'">
                                    <span class="d-inline badge text-bg-warning form-separator">{{leagueRank.form[0]}}</span>
                                </span>

                                <span v-if="leagueRank.form[0] == 'L'">
                                    <span class="d-inline badge text-bg-danger form-separator">{{leagueRank.form[0]}}</span>
                                </span>

                                <span v-if="leagueRank.form[1] == 'W'">
                                    <span class="d-inline badge text-bg-success form-separator">{{leagueRank.form[1]}}</span>
                                </span>
                                
                                <span v-if="leagueRank.form[1] == 'D'">
                                    <span class="d-inline badge text-bg-warning form-separator">{{leagueRank.form[1]}}</span>
                                </span>

                                <span v-if="leagueRank.form[1] == 'L'">
                                    <span class="d-inline badge text-bg-danger form-separator">{{leagueRank.form[1]}}</span>
                                </span>

                                <span v-if="leagueRank.form[2] == 'W'">
                                    <span class="d-inline badge text-bg-success form-separator">{{leagueRank.form[2]}}</span>
                                </span>
                                
                                <span v-if="leagueRank.form[2] == 'D'">
                                    <span class="d-inline badge text-bg-warning form-separator">{{leagueRank.form[2]}}</span>
                                </span>

                                <span v-if="leagueRank.form[2] == 'L'">
                                    <span class="d-inline badge text-bg-danger form-separator">{{leagueRank.form[2]}}</span>
                                </span>

                                <span v-if="leagueRank.form[3] == 'W'">
                                    <span class="d-inline badge text-bg-success form-separator">{{leagueRank.form[3]}}</span>
                                </span>
                                
                                <span v-if="leagueRank.form[3] == 'D'">
                                    <span class="d-inline badge text-bg-warning form-separator">{{leagueRank.form[3]}}</span>
                                </span>

                                <span v-if="leagueRank.form[3] == 'L'">
                                    <span class="d-inline badge text-bg-danger form-separator">{{leagueRank.form[3]}}</span>
                                </span>

                                <span v-if="leagueRank.form[4] == 'W'">
                                    <span class="d-inline badge text-bg-success form-separator">{{leagueRank.form[4]}}</span>
                                </span>
                                
                                <span v-if="leagueRank.form[4] == 'D'">
                                    <span class="d-inline badge text-bg-warning form-separator">{{leagueRank.form[4]}}</span>
                                </span>

                                <span v-if="leagueRank.form[4] == 'L'">
                                    <span class="d-inline badge text-bg-danger form-separator">{{leagueRank.form[4]}}</span>
                                </span>
                            
                            </td>
                        </tr>
                </tbody>

                <tbody v-if="standing.league_type == 'Cup'" v-for="cupRank in standing.standings">
                    
                        <tr class="standings-header">
                            <td colspan="2">{{cupRank[0].group}}</td>
                            <td>MP</td>
                            <td>W</td>
                            <td>D</td>
                            <td>L</td>
                            <td>G</td>
                            <td>+/-</td>
                            <td>P</td>
                            <td></td>
                        </tr>

                        <tr class="team-rank-row" v-for="cup in cupRank">
                            <td class="standings-rank">{{cup.rank}}</td>
                            <td class="standings-team-name"><img class="standings-team-logo" v-bind:src="cup.team.logo" alt="team-logo" /> {{cup.team.name}}</td>
                            <td>{{cup.all.played}}</td>
                            <td>{{cup.all.win}}</td>
                            <td>{{cup.all.draw}}</td>
                            <td>{{cup.all.lose}}</td>
                            <td>{{cup.all.goals.for}}:{{cup.all.goals.against}}</td>
                            <td>{{cup.goalsDiff}}</td>
                            <td>{{cup.points}}</td>
                            <td>
                                <span v-if="cup.form[0] == 'W'">
                                    <span class="d-inline badge text-bg-success form-separator">{{cup.form[0]}}</span>
                                </span>

                                <span v-if="cup.form[0] == 'D'">
                                    <span class="d-inline badge text-bg-warning form-separator">{{cup.form[0]}}</span>
                                </span>

                                <span v-if="cup.form[0] == 'L'">
                                    <span class="d-inline badge text-bg-danger form-separator">{{cup.form[0]}}</span>
                                </span>

                                <span v-if="cup.form[1] == 'W'">
                                    <span class="d-inline badge text-bg-success form-separator">{{cup.form[1]}}</span>
                                </span>

                                <span v-if="cup.form[1] == 'D'">
                                    <span class="d-inline badge text-bg-warning form-separator">{{cup.form[1]}}</span>
                                </span>

                                <span v-if="cup.form[1] == 'L'">
                                    <span class="d-inline badge text-bg-danger form-separator">{{cup.form[1]}}</span>
                                </span>

                                <span v-if="cup.form[2] == 'W'">
                                    <span class="d-inline badge text-bg-success form-separator">{{cup.form[2]}}</span>
                                </span>

                                <span v-if="cup.form[2] == 'D'">
                                    <span class="d-inline badge text-bg-warning form-separator">{{cup.form[2]}}</span>
                                </span>

                                <span v-if="cup.form[2] == 'L'">
                                    <span class="d-inline badge text-bg-danger form-separator">{{cup.form[2]}}</span>
                                </span>
                            </td>
                        </tr>
                </tbody>
        </table>
    </div>

</template>
<script>
export default {
    name: 'Standings',
    data(){
        return{
            currentStandings: []
        }
    },
    methods: {
        getStandings(leagueId, season){

            this.getLeagueTypeById(leagueId).then(r => {
                    const { response } = r
                    //iteramos la liga
                    for (let index = 0; index < response.length; index++) {
                            const obj_standings = this.objStandings()
                            obj_standings.league_type = response[index].league.type

                            //lamamos las estadisticas por id de liga
                            this.getStandingsById(leagueId, season).then(r => {
                            const { response } = r
                            
                                this.currentStandings = []
                             
                                for (let i = 0; i < response.length; i++) {
                                    
                                    obj_standings.league_id = response[i].league.id
                                    obj_standings.league_name = response[i].league.name
                                    obj_standings.league_country = response[i].league.country
                                    obj_standings.league_logo = response[i].league.logo
                                    obj_standings.country_flag = response[i].league.flag
                                    obj_standings.league_season = response[i].league.season

                                    for (let j = 0; j < response[i].league.standings.length; j++) {
                                            if(obj_standings.league_type == 'Cup'){
                                                obj_standings['standings'][j] = response[i].league.standings[j]
                                            }

                                        for (let k = 0; k < response[i].league.standings[j].length; k++) {
                                            if(obj_standings.league_type == 'League'){
                                                obj_standings['standings'][k] = {
                                                                                    rank: response[i].league.standings[j][k].rank,
                                                                                    team_id: response[i].league.standings[j][k].team.id,
                                                                                    team_name: response[i].league.standings[j][k].team.name,
                                                                                    team_logo: response[i].league.standings[j][k].team.logo,
                                                                                    points: response[i].league.standings[j][k].points,
                                                                                    goals_diff: response[i].league.standings[j][k].goalsDiff,
                                                                                    group: response[i].league.standings[j][k].group,
                                                                                    form: response[i].league.standings[j][k].form,
                                                                                    status: response[i].league.standings[j][k].status,
                                                                                    description: response[i].league.standings[j][k].description,
                                                                                    all_played: response[i].league.standings[j][k].all.played,
                                                                                    all_win: response[i].league.standings[j][k].all.win,
                                                                                    all_draw: response[i].league.standings[j][k].all.draw,
                                                                                    all_lose: response[i].league.standings[j][k].all.lose,
                                                                                    all_goals_for: response[i].league.standings[j][k].all.goals.for,
                                                                                    all_goals_against: response[i].league.standings[j][k].all.goals.against
                                                                                }
                                            }
                                        }
                                    }
                                    
                                    this.currentStandings.push(obj_standings)
                                }       
                            })
                    }
            })
        },
        async getStandingsById(leagueId, season){

            const url = `https://v3.football.api-sports.io/standings?league=${leagueId}&season=${season}`

            const myHeaders = new Headers();
            myHeaders.append("x-rapidapi-key", "7407d9aed0212543ced8ebf43ca11c0a");
            myHeaders.append("x-rapidapi-host", "v3.football.api-sports.io");

            const requestOptions = {
                                        method: 'GET',
                                        headers: myHeaders,
                                        redirect: 'follow'
                                    }
            const response = await fetch(url, requestOptions);
            return response.json()
        },
        async getLeagueTypeById(leagueId){
            const url = `https://v3.football.api-sports.io/leagues?id=${leagueId}`

            const myHeaders = new Headers();
            myHeaders.append("x-rapidapi-key", "7407d9aed0212543ced8ebf43ca11c0a");
            myHeaders.append("x-rapidapi-host", "v3.football.api-sports.io");

            const requestOptions = {
                                        method: 'GET',
                                        headers: myHeaders,
                                        redirect: 'follow'
                                    }
            const response = await fetch(url, requestOptions);
            return response.json()
        },
        objStandings(){
            return{
                league_id: '',
                league_name: '',
                league_country: '',
                league_logo: '',
                country_flag: '',
                league_season: '',
                league_type: '',
                standings: []
            }
        }
    }
}
</script>