<template>
    
    <div class="container">
        <div class="text-center text-datepicker mb-4">
            Use the datepicker to browse matches
        </div>
        
        <!--datepicker-->
        <a class="nav-link dropdown-toggle mb-4" href="#" id="navbarDarkDropdownMenuLink" role="button"
            data-bs-toggle="dropdown" aria-expanded="false">
            Datepicker <font-awesome-icon icon="fa-solid fa-calendar-days" />
        </a>
        <ul class="dropdown-menu" aria-labelledby="navbarDarkDropdownMenuLink">
            <li v-for="date in allDates">
                <a class="dropdown-item" href="#" v-on:click="getFixturesByDate(date)">{{ date }}</a>
            </li>
        </ul>

        <!-- Standings Modal -->
        <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true" data-bs-backdrop="static">
        <div class="modal-dialog modal-dialog-centered modal-xl">
            <div class="modal-content">
            <div class="modal-header">
                <h1 class="modal-title fs-5" id="exampleModalLabel">Standings</h1>
                <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div class="modal-body">
                <Standings ref="standingsComponent"/>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
            </div>
            </div>
        </div>
        </div>
        <!--endModal-->

        <ul class="nav nav-tabs" id="myTab" role="tablist">
            
            <li class="nav-item" role="presentation">
                <button class="nav-link active" id="all-tab" data-bs-toggle="tab" data-bs-target="#all-tab-pane"
                    type="button" role="tab" aria-controls="all-tab-pane" aria-selected="true">All</button>
            </li>
            <!--
            <li class="nav-item" role="presentation">
                <button class="nav-link"  id="live-tab" data-bs-toggle="tab" data-bs-target="#live-tab-pane"
                    type="button" role="tab" aria-controls="live-tab-pane" aria-selected="false" >Live</button>
            </li>
            
            
            <li class="nav-item" role="presentation">
                <button class="nav-link" id="finished-tab" data-bs-toggle="tab" data-bs-target="#finished-tab-pane"
                    type="button" role="tab" aria-controls="finished-tab-pane" aria-selected="false" disabled>Finished</button>
            </li>

    
            <li class="nav-item" role="presentation">
                <button class="nav-link" id="scheduled-tab" data-bs-toggle="tab" data-bs-target="#scheduled-tab-pane"
                    type="button" role="tab" aria-controls="scheduled-tab-pane" aria-selected="false" disabled>Scheduled</button>
            </li>
            -->
   
        </ul>
    
        <div class="tab-content" id="myTabContent">
            <div class="tab-pane fade show active" id="all-tab-pane" role="tabpanel" aria-labelledby="all-tab" tabindex="0">
                <div class="text-center mb-4">
                    <strong>{{selectedDate == null ? '' : selectedDate}}</strong>
                </div>

                <!--fixtures-->
                <div v-for="fixture in allFixtures">
                    <table class="table table-sm align-middle">
                        <thead>
                            <tr>
                                <th colspan="5">
                                    <span v-if="fixture.league.flag != null">
                                        <img class="league-logo" v-bind:src="fixture.league.flag" alt="league-logo" />
                                    </span>

                                    <span v-else="">
                                        <img class="league-logo" v-bind:src="fixture.league.logo" alt="league-logo" />
                                    </span>
                                    {{fixture.league.name}}
                                    
                                
                                </th>
                                <td class="text-center standings-link" width="10%"><a href="#" data-bs-toggle="modal" data-bs-target="#exampleModal" v-on:click="leagueStandings(fixture.league.id, fixture.league.season)">Standings</a></td>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td rowspan="2" width="10%" class="text-center status-code">{{fixture.fixture.status.short == 'NS' ? fixture.fixture.date.split('T')[1].substr(0,5) : fixture.fixture.status.short }}</td>
                                <td><img class="team-logo" v-bind:src="fixture.teams.home.logo" alt="team-home-logo" /> {{fixture.teams.home.name}}</td>
                                <td></td>
                                <td></td>
                                <td class="text-end">{{fixture.goals.home != null ? fixture.goals.home : '-'}}</td>
                                <td rowspan="2" width="10%" class="text-center"><font-awesome-icon icon="fa-solid fa-shirt"/></td>
                            </tr>
                            <tr>
                                
                                <td><img class="team-logo" v-bind:src="fixture.teams.away.logo" alt="team-away-logo" /> {{fixture.teams.away.name}}</td>
                                <td></td>
                                <td></td>
                                <td class="text-end">{{fixture.goals.away != null ? fixture.goals.away : '-'}}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <!--end fixtures-->
                    
                    
                    

            </div>

            <div class="tab-pane fade" id="live-tab-pane" role="tabpanel" aria-labelledby="live-tab" tabindex="0">
                <span>Live games coming soon ...</span>
            </div>

            <div class="tab-pane fade" id="finished-tab-pane" role="tabpanel" aria-labelledby="finished-tab" tabindex="0">
                <span>Finished Games</span>
            </div>
            
            <div class="tab-pane fade" id="scheduled-tab-pane" role="tabpanel" aria-labelledby="scheduled-tab" tabindex="0">
                <span>Scheduled</span>
            </div>
        </div>
    
    </div>
</template>

<script>
import Standings from '@/components/Standings'
import Fixtures from '@/components/Fixtures'

export default {
    components: {
        Standings
    },
    data() {
        return {
            today: null,
            allDates: [],
            selectedDate: null,
            allFixtures: []
        }
    },
    mounted() {
        this.getAllDates(),
        this.getFixturesByDate(this.today)
    },
    methods: {
        getAllDates(){
            const todayDate = new Date()
            
            this.today = todayDate.toLocaleDateString('en-CO', {
                year: 'numeric',
                month: 'short',
                day: '2-digit',
                timeZone: 'America/Bogota'
            })
            
            for (let i = 0; i < 15; i++) {
                const allDates = new Date();
                allDates.setDate(allDates.getDate() - (7 - i))
                
                
                this.allDates.push(allDates.toLocaleDateString('en-CO', {
                    year: 'numeric',
                    month: 'short',
                    day: '2-digit',
                    timeZone: 'America/Bogota'
                }))
                
            }
        },
        getFixturesByDate(date){
            const selectedDate = new Date(date)
            const formatedDate = selectedDate.toISOString().split('T')[0]
            this.fixturesByDate(formatedDate).then(fixtures => {
                const { response } = fixtures

                const sortedFixtures = response.sort(((a, b) => a.league.id - b.league.id));

                this.allFixtures = []
            
                for (let i = 0; i < sortedFixtures.length; i++) {
                        switch (sortedFixtures[i].league['id']) {
                                                        
                            case 1: //World - world Cup
                                this.allFixtures.push(response[i])
                            break;

                            case 2: //UEFA Champions League
                                this.allFixtures.push(response[i])
                            break;

                            case 3: //UEFA Europa League
                                this.allFixtures.push(response[i])
                            break;

                            case 4: //Euro Championship
                                this.allFixtures.push(response[i])
                            break;

                            case 9: //Copa America
                                this.allFixtures.push(response[i])
                            break;

                            case 11: //CONMEBOL Sudamericana
                                this.allFixtures.push(response[i])
                            break;

                            case 179: //Scotland Premieship
                                this.allFixtures.push(response[i])
                            break;

                            case 239: //Colombia - primera A
                                this.allFixtures.push(response[i])
                            break;

                            case 84: //pruebas
                                this.allFixtures.push(response[i])
                            break;

                            case 85: //pruebas
                                this.allFixtures.push(response[i])
                            break;

                            default:
                                break;
                        }
                }
            })
        },
        async fixturesByDate(date){
            const selectedDateByUser = new Date(date.toString())
            
            this.selectedDate = selectedDateByUser.toLocaleDateString('en-CO', {
                year: 'numeric',
                month: 'short',
                day: '2-digit',
                timeZone: 'UTC'
            })

            const url = `https://v3.football.api-sports.io/fixtures?date=${date}&timezone=America/Bogota`

            const myHeaders = new Headers();
            myHeaders.append("x-rapidapi-key", "7407d9aed0212543ced8ebf43ca11c0a");
            myHeaders.append("x-rapidapi-host", "v3.football.api-sports.io");

            const requestOptions = {
                                        method: 'GET',
                                        headers: myHeaders,
                                        redirect: 'follow'
                                    }
            const response = await fetch(url, requestOptions);
            return response.json()
        },
        leagueStandings(leagueId, season){
            this.$refs.standingsComponent.getStandings(leagueId, season)
        }
    }
}
</script>