<template>
    <nav class="navbar navbar-dark navbar-expand-lg sticky-top bg-blue">
        <div class="container">
            <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarTogglerDemo03"
                aria-controls="navbarTogglerDemo03" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <router-link class="navbar-brand" to="/">
                <img src="@/assets/img/logo.png" class="navbar-logo" alt="" srcset="">
            </router-link>

            <div class="collapse navbar-collapse" id="navbarTogglerDemo03" :class="!visible ? 'collapse' : ''">
                <ul class="navbar-nav me-auto mb-2 mb-lg-0">
                    <li class="nav-item">
                        <router-link class="nav-link" :class="{active: isActiveHome }" @click="visible = !visible; activeLinkMethod('home')" to="/" >Home</router-link>
                    </li>

                    <li class="nav-item">
                        <router-link class="nav-link" :class="{active: isActiveAbout }" @click="visible = !visible; activeLinkMethod('about')" to="/about" >About</router-link>
                    </li>

                </ul>
                <form class="d-flex" role="search">
                    <a href="https://twitter.com/xaero666" target="_blank"><font-awesome-icon :icon="['fab', 'twitter']" class="twitter-icon" /></a>
                </form>
            </div>
        </div>
    </nav>
  <router-view/>   
</template>

<script>
export default {
    name: 'NavigationBar',
    data(){
        return{
            isActiveHome: true,
            isActiveAbout: false,
            visible: false
        }
    },
    methods:{
        activeLinkMethod(section){
            if(section == 'about'){
                this.isActiveAbout = !this.isActiveAbout;
                this.isActiveHome = !this.isActiveHome;
                
            }else if(section == 'home'){
                this.isActiveAbout = !this.isActiveAbout;
                this.isActiveHome = !this.isActiveHome;
            }
        }
    }
}
</script>
